@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@300;400;500;600;700;800&family=Open+Sans:wght@300;400;500;600;700&display=swap");
*, *::after, *::before {
  padding: 0px;
  margin: 0px;
  position: relative;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Noto Sans", sans-serif;
  color: #fff;
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  align-items: center;
}

section.mobileContainer {
  padding: 10px 20px;
  padding-top: 0px;
  overflow: hidden;
  min-width: 320px;
  min-height: 550px;
  height: 550px;
  border-radius: 25px;
  background: #000;
}

.header {
  height: auto;
  background-color: #000;
  border-top-right-radius: 25px;
  border-top-left-radius: 25px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
}
.header .iconsCon {
  width: 100%;
  height: 30px;
}
.header .navBtns {
  width: 100%;
}
.header .navBtns button {
  height: 100%;
  height: 30px;
  width: 30px;
  font-size: 20px;
  color: #fff;
  background: none;
  cursor: pointer;
  padding: 0px;
  text-align: start;
  outline: none;
  border: none;
}

.title_profile_con {
  height: 80px;
  display: grid;
  grid-template-columns: 1fr auto;
  justify-content: space-between;
  align-items: center;
}
.title_profile_con h2 {
  padding: 0px;
  font-size: 26px;
  font-weight: 700;
}
.title_profile_con .profileImage {
  margin-bottom: 6px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: grid;
  justify-items: center;
  align-items: center;
  color: #000;
  background: #ddd;
  font-size: 16px;
}

.AmountDisplay {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 4px;
}
.AmountDisplay h6 {
  color: #ddd;
  font-weight: 400;
  font-size: 12px;
}

.cardsContainerWrapper {
  margin-top: 15px;
  width: 100%;
  height: 300px;
}
.cardsContainerWrapper .cardsMainContainerScroll {
  overflow-x: scroll;
  overflow-y: hidden;
  position: absolute;
  top: 0px;
  left: -20px;
  width: calc(100% + 40px);
  height: 100%;
  list-style-type: none;
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-template-rows: 1fr;
  padding-bottom: -20px;
}

.card {
  margin-left: 20px;
  display: inline-block;
  height: 300px;
  width: 200px;
  border-radius: 10px;
  cursor: pointer;
}
.card:nth-child(4) {
  border: none !important;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.cardsMainContainerScroll::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.cardsMainContainerScroll {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.cardsMainContainerScroll {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
}

.card1 {
  background-color: rgb(255, 136, 0);
}

.card2 {
  background-color: rgb(0, 157, 255);
}

.card3 {
  background-color: rgb(73, 69, 69);
}

.CButtonsCon {
  margin-top: 8px;
  width: 32px;
  height: 30px;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: 1fr;
  overflow: hidden;
  justify-content: space-between;
  align-items: center;
}
.CButtonsCon .ctbn {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: rgb(126, 124, 124);
}

ul.listSectionFirst {
  display: none;
  list-style-type: none;
  width: 100%;
}
ul.listSectionFirst li {
  height: auto;
  width: 100%;
  border: 1px solid red;
}